import React, { useState } from "react";
import { NavLink } from "react-router-dom";
import "../style/Navbar.css";


const Navbar = () => {
    const [isMenu, setIsMenu] = useState(false);

    const handleClick = () => {
        setIsMenu(!isMenu);
    };
    return (
        <nav className="navbar">
            <div className="navbar__container">
                    <NavLink
                        to="/"
                    >
                        <div className="navbar__logo">
                            <div className="navbar__logo--name">
                                <div className="square"></div>Kieu Dac
                        </div>
                            <div className="navbar__logo--position">
                                <span>/</span> FRONT-END DEVELOPER
                            </div>
                        </div>
                    </NavLink>
                    
                <ul className={isMenu ? "navbar__menu active" : "navbar__menu"}>
                    <li className="navbar__item">
                        <NavLink
                            exact
                            to="/"
                            className="navbar__link"
                            activeClassName="navbar__link--active"
                            onClick={handleClick}
                        >
                            About me
                        </NavLink>
                    </li>
                    <li className="navbar__item">
                        <NavLink
                            to="/resume"
                            className="navbar__link"
                            activeClassName="navbar__link--active"
                            onClick={handleClick}
                        >
                            Resume
                        </NavLink>
                    </li>
                    <li className="navbar__item">
                        <NavLink
                            to="/project"
                            className="navbar__link"
                            activeClassName="navbar__link--active"
                            onClick={handleClick}
                        >
                            Projects
                        </NavLink>
                    </li>
                    <li className="navbar__item">
                        <NavLink
                            to="/contact"
                            className="navbar__link"
                            activeClassName="navbar__link--active"
                            onClick={handleClick}
                        >
                            Contact
                        </NavLink>
                    </li>   
                </ul>
                <div className={isMenu ? 'navbar__icon active' : 'navbar__icon'} onClick={handleClick}>
                    
                <button className="menu"  aria-label="Main Menu">
                <svg width="45" height="45" viewBox="0 0 100 100">
                    <path className="line line1" d="M 20,29.000046 H 80.000231 C 80.000231,29.000046 94.498839,28.817352 94.532987,66.711331 94.543142,77.980673 90.966081,81.670246 85.259173,81.668997 79.552261,81.667751 75.000211,74.999942 75.000211,74.999942 L 25.000021,25.000058" />
                    <path className="line line2" d="M 20,50 H 80" />
                    <path className="line line3" d="M 20,70.999954 H 80.000231 C 80.000231,70.999954 94.498839,71.182648 94.532987,33.288669 94.543142,22.019327 90.966081,18.329754 85.259173,18.331003 79.552261,18.332249 75.000211,25.000058 75.000211,25.000058 L 25.000021,74.999942" />
                </svg>
                </button>
                    
                </div>
            </div>
        </nav>
    );
};

export default Navbar;
